<template>
	<div id="cart_nodata">
		<div class="cart_content">
			
			
			<div class="hot_food">
				
				<h3>本店顾客最长点的菜</h3>
				
				<div class="item_list_outer">
					<ul class="item_list">
						<li v-for="item in list">
							<router-link :to="'/gouwuche/'+item.caidan_id">
								<div class="inner">
										<img :src="url+item.caidan_img">
									<p class="title">{{item.caidan_name}}</p>						
									<p class="price">¥{{item.caidan_jiag}}</p>
								</div>
							</router-link>	
						</li>
						
						
						
					</ul>
					
				</div>
				
			</div>
			
			
			<div class="cart_empty" v-if="num<1">
				<h3>购物车空空的 </h3>
				
				<p>您可以点击下面的菜单开始点菜</p>
			</div>
			
		</div>
		<!-- <v-dh></v-dh> -->
		
	</div>
</template>
<script>
// import Config from "@/medol/config.js"
// import Dh from "./dh.vue"
export default{
	data(){
		return{
			// url:Config.url,
			list:[],
			url:'http://localhost:8080/web/'
		}
	},
	props:['num'],
	components:{
		// "v-dh":Dh
	},
	methods:{
		getData(){
			this.$http.get('http://localhost:8080/web/hotlist.php').then((success)=>{
				this.list=success.body;
			},(err)=>{
				console.log(err)
			})
		}
	},
	mounted(){
		this.getData();
	}
}
</script>
<style  scoped>
		.clearfix:after {
		content: "";
		display: block;
		visibility: hidden;
		height: 0;
		clear: both; }
		  .footer_nav {
		height: 4.4rem;
		width: 4.4rem;
		background: #000;
		position: fixed;
		color: #fff;
		bottom: .5rem;
		left: .5rem;
		text-align: center;
		border-radius: 50%; }
		.footer_nav img {
		  width: 1.8rem;
