<template>
    <div id="app">
      <div class="footer">
          <ul>
            <li v-for = "item in list">
                <img :src="item.img" alt="flowers"><br>
                <router-link :to="{ path: item.path }" class="linkspan">
                  <span>{{item.name}}</span>
                </router-link>
            </li>
          </ul>
      </div>
      <div class="item" v-for="item in cailm">
        <h3>{{item.cailx_name}}</h3>
        <ul>
          <li v-for="item1 in caidan">
            <router-link :to="'gouwuche/'+item1.caidan_id" style="text-decoration: none; color: #444;" v-if="item.cailx_id==item1.cailx_id" >
              <img :src="url+item1.caidan_img" alt="flowers"><br>
              <p style="font-weight: bold;">{{item1.caidan_name}}</p>
              <p style="margin: .5rem 0rem .2rem 0rem;">{{item1.caidan_jiag}}</p>
            </router-link>
          </li>
        </ul>
      </div>
      <div v-bind:class="{'active': isActive, 'sidebar': hasError}">
        <div class="sidebar-an" @click="applyses()">
          <img :src="sidebaranimg" alt="">
          <p>菜单</p>
        </div>
        <ul>
          <li v-for = "sili in sidli">{{sili}}</li>
        </ul>
      </div>
      <div class="foo le">
        <img :src="footerimg" alt="">
        <p>导航</p>
      </div>
      <router-link :to="{path: '/dian'}">
      <div class="foo ri">
          <img :src="footerrimg" alt="">
          <p>购物车</p>
      </div></router-link>
        <!-- <img :src="url+'img/2.jpg'" alt=""> -->
    </div>
    
  </template>
  
  <script>
    import temles from "@/imgag/temles.js"
  export default {
    name: 'app',
    data () {
      return {
        isActive:true,
        hasError:false,
        cailm:[],
        caidan:[],
        url:'http://localhost:8080/web/',
        sidebaranimg:"./img/nav.png",
        list:[
            {name:"热销榜", img:"../static/img/rexiao.png","path":'/rexiao'},
            {name:'点过的菜', img:"../static/img/caidan.png","path":'/dianguo'},
            {name:'搜你喜欢',img:"../static/img/sousuo.png","path":'/shousuo'}
          ],
        sidli:['精品美食', '精品小面', '饮料酒水', '新品推荐', '舌尖美食', '厨师长推荐'],
        footerimg:"../static/img/navigation.png",
        footerrimg:"../static/img/cart.png"
      }
    },
    components:{
        
    },
    methods:{
      applyses(){
  				this.isActive=!this.isActive,
          this.hasError=!this.hasError
        },
        // toBFun(e){
        //   var menuname1=e.currentTarget.children[2].innerHTML;
        //   var menuname2=e.currentTarget.children[3].innerHTML;
        //   this.$router.push({name:'gouwuche',params:{menuname1:menuname1,menuname2:menuname2}});
        // },
        // getdate(){
        //   this.$http.get(this.rul+'web/caidan.php').then((response))
        // }
        getdate(){
          this.$http.get('http://localhost:8080/web/caidan.php').then((response)=>{
            console.log(response);
            this.caidan=response.body;
          },(err)=>{
            console.log(err);
          })
        },
        getLm(){
          this.$http.get('http://localhost:8080/web/cailm.php').then((response)=>{
            console.log(response);
            this.cailm=response.body;
          },(err)=>{
            console.log(err);
          })
        }
    },
    created() {
     this.getLm();
     this.getdate(); 
    }
  }
  </script>
<style scoped>
    * {
        margin: 0px;
        padding: 0px;
    }

    .footer {
        height: 4rem;
        border-radius: .5rem;
        width: 96%;
        margin: 0 auto 0 auto;
        background: #FFF;
    }
    .footer ul {
       display: flex;
       list-style: none;
    }

    .footer ul li{
        border-left: 1px solid #F7F7F7;
        text-align: center;
        width: 32%;
        float: left;
    }

    .footer img {
        margin-top: .5rem;
        width: 2rem;
        height: 2rem;
    }

    .footer span {
      font-size: .9rem;
    }

    h3 {
      color: #444;
      margin-top: .5rem;
      text-align: center;
      margin-bottom: .5rem;
    }

    .item {
      clear: both;
    }

    .item ul {
      list-style: none;
      padding: 0 .3rem 0 .3rem;
    }

    .item ul li {
      margin: 0rem 0rem 0rem 0.2rem;
      width: 32%;
      background: #FFF;
      float: left;
      
    }

    .item ul li img {
      width: 100%;
      border-radius: .5rem .5rem 0 0;
    }

    .item ul li p {
      font-size: .5rem;
      margin: .5rem 0rem 0rem 0rem;
    }

    .itemt h3 {
      font-weight: normal;
      margin-top: 1rem;
      font-size: .9rem;
    }

    .sidebar {
      transition: all 1s;
      z-index: 2;
      width: 6rem;
      height: 100%;
      position: fixed;
      background: #eee;
      top: 0px;
      left: 0px;
    }

    .active {
      transition: all 1s;
      z-index: 2;
      width: 6rem;
      height: 100%;
      position: fixed;
      background: #eee;
      top: 0px;
      left: -95px;
    }

    .sidebar ul {
      list-style: none;
    }

    .sidebar ul li {
      text-align: center;
      line-height: 5rem;
      height: 5rem;
    }

    .sidebar-an {
      position: absolute;
      right: -2.5rem;
      background: rgba(132, 128, 128, 0.9);
      top: 40%;
      width: 3rem;
      height: 3rem;
      text-align: center;
      border-radius: 0rem 50% 50% 0rem;
      z-index: 2;
    }

    .sidebar-an img {
      margin: 0rem 1rem 0rem 0rem;
      width: 70%;
    }

    .sidebar-an p {
      margin: -.5rem .5rem 0rem 0rem;
    }

    .foo {
      text-align: center;
      position: fixed;
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      color: #fff;
    }

    .le {
      display: none;
      opacity: 0.7;
      margin-left: -13vw;
      bottom: 5vw;
      left: 20%;
      background: black;
    }

    .ri {
      margin-left: -13vw;
      bottom: 5vw;
      left: 90%;
      background: red;
    }
    .foo img {
      margin: 1vw 0vw 0vw 0vw;
      width: 50%;
    }
    .router {
      color: #fff;
      text-decoration: none;
    }

    .foo p {
      font-size: .5rem;
    }

    .footer .linkspan {
      text-decoration: none;
      color: #000;
      font-size: .9rem;
    }
</style>


