<template>
  <div id="app">
    <div class="cart_content">
			
			<div class="cart_info">
				
				
				<h2>购物车</h2>				
				
				<div class="p_number">
					<div class="p_number_left">
	       				<p>用餐人数:2人</p>
	       				<p>备注:无</p>
	       			</div>	       			
	       			<div class="p_number_right">
	       				<img src="../static/img/edit.png"/>
	       				<p>修改</p>
	       			</div>					
					
				</div>
				
				<div class="cart_p_num">
					
					<p>购物车中总共有6个菜</p>
	       			
	       			<p>合计：<span class="price">¥58</span></p>
				</div>
			</div>
			
			
			
			<div class="cart_list">
				
				
				<ul>
					<li class="item" v-for="(item,key) in list">
						<div class="left_food">
	       			<img :src="url+item.caidan_img">       						
		       		<div class="food_info">
		       			<p>{{item.caidan_name}}</p>
		       			<p class="price">¥{{item.caidan_jiag}}</p>
		       		</div>
	       		</div>
	       						
	       		<div class="right_cart">
	       			<div class="cart_num">
						    <div class="input_left" @click="jienum(key)">-</div>
						     <div class="input_center">
                    <input type="text"  readonly="readonly" v-model="item.num" name="num" id="num" />
						     </div>
						     <div class="input_right" @click="item.num++">+</div>	  
						  </div>	
	       		</div>	
					</li>
				</ul>
			</div>
      <v-nodata :num="num"></v-nodata>
		</div>
		
		
		<div id="footer_nav" class="footer_nav">
			<img src="../static/img/navigation.png"/>
			<p>导航</p>
		</div>
			
		<router-link :to="{path: '/dianchan'}">
		<div id="footer_book" class="footer_book">
			<img src="../static/img/menu.png"/>
			<p>菜单</p>
		</div></router-link>
		
		
		
		<div id="footer_cart" class="footer_cart">
			<img src="../static/img/cart.png"/>
			<p>购物车</p>
		</div>
  </div>
</template>

<script>
  import NoData from "@/components/zicomponents/cart_nodata.vue"
  import Storage from "@/imgag/storage.js"
export default {
  name: 'app',
  data () {
    return {
      msg: {
                    num: 1,
                    flag: true
                },
      list:[],
      url:'http://localhost:8080/web/',
    }
  },
  computed:{
		num:function(){
			return this.list.length;
		}
	},
  components:{
    "v-nodata":NoData
  },
  methods:{
    a () {
      this.msg.num = this.msg.num - 1;
    },

    b () {
      this.msg.num = this.msg.num + 1;
    },
    getData(){
			var zhou_id=Storage.get("zhuo_id");
			var diancai_id=Storage.get('diancai_id');
			var str={
				zhuo_id:zhou_id,
				diancai_id:diancai_id
			}
      console.log(str)
			this.$http.get('http://localhost:8080/web/cart.php',{params:str},{emulateJSON:true}).then((success)=>{
				console.log(success)
				this.list=success.body;
			},(err)=>{

			})
		},
    jienum(key){
			if(this.list[key].num>=1){
				this.list[key].num--;
			}
		},
    updateData(){
			var list={
				list:this.list
			}
			this.$http.post('http://localhost:8080/web/cart.php',list,{emulateJSON:true}).then((success)=>{
				for (var i = this.list.length - 1; i >= 0; i--) {
					if(this.list[i].num<1){
						this.list.splice(i,1);
					}
				}
			},(err)=>{
				console.log(err);
			})
		}
  },
  mounted(){
		this.getData();
	},
  updated(){
		this.updateData();
	}
}
</script>

<style  scoped>
  .clearfix:after {
  content: "";
  display: block;
  visibility: hidden;
  height: 0;
  clear: both; }
    .footer_nav {
  height: 4.4rem;
  width: 4.4rem;
  background: #000;
  position: fixed;
  color: #fff;
  bottom: .5rem;
  left: .5rem;
  text-align: center;
  border-radius: 50%; }
  .footer_nav img {
    width: 1.8rem;
    height: 1.8rem;
    margin-top: .4rem; }
  .footer_nav p {
    position: relative;
    top: -0.2rem; }

/*导航弹出层*/
.footer_nav_show {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  background: rgba(0, 0, 0, 0.6);
  z-index: 2; }
  .footer_nav_show .list li {
    height: 4.4rem;
    width: 4.4rem;
    background: #000;
    position: absolute;
    color: #fff;
    left: .5rem;
    text-align: center;
    border-radius: 50%; }
    .footer_nav_show .list li img {
      width: 1.8rem;
      height: 1.8rem;
      margin-top: .4rem; }
    .footer_nav_show .list li p {
      position: relative;
      top: -0.2rem;
      font-size: 1rem; }
    .footer_nav_show .list li:nth-child(1) {
      bottom: 15.4rem;
      left: 0px; }
    .footer_nav_show .list li:nth-child(2) {
      bottom: 12.4rem;
      left: 30%;
      margin-left: -2.2rem; }
    .footer_nav_show .list li:nth-child(3) {
      bottom: 7.4rem;
      left: 45%;
      margin-left: -2.2rem; }
    .footer_nav_show .list li:nth-child(4) {
      left: 50%;
      margin-left: -2.2rem;
      bottom: .5rem; }
    .footer_nav_show .list li:nth-child(5) {
      left: .5rem;
      bottom: .5rem; }

.footer_cart {
  height: 4.4rem;
  width: 4.4rem;
  background: red;
  position: fixed;
  color: #fff;
  bottom: .5rem;
  right: .5rem;
  text-align: center;
  border-radius: 50%; }
  .footer_cart img {
    width: 1.8rem;
    height: 1.8rem;
    margin-top: .4rem; }
  .footer_cart p {
    position: relative;
    top: -0.2rem; }

.footer_book {
  height: 4.4rem;
  width: 4.4rem;
  background: #000;
  position: fixed;
  color: #fff;
  bottom: .5rem;
  left: 50%;
  margin-left: -2.2rem;
  text-align: center;
  border-radius: 50%; }
  .footer_book img {
    width: 1.8rem;
    height: 1.8rem;
    margin-top: .4rem; }
  .footer_book p {
    position: relative;
    top: -0.2rem; }
    .cart_content {
  padding: 1rem;
  /*头部信息*/
  /*购物车列表*/
  /*最长点的菜*/ }
  .cart_content .cart_info {
    background: #fff;
    border-radius: .5rem;
    padding: .5rem;
    /*购物车总数量*/ }
    .cart_content .cart_info h2 {
      text-align: center;
      font-size: 1.8rem;
      padding: .8rem 0px;
      border-bottom: 1px solid #eee; }
    .cart_content .cart_info .p_number {
      display: flex;
      padding: .5rem 0;
      border-bottom: 1px solid #eee; }
      .cart_content .cart_info .p_number .p_number_left {
        flex: 1; }
        .cart_content .cart_info .p_number .p_number_left p {
          line-height: 2; }
          .cart_content .cart_info .p_number .p_number_left p:first-child {
            color: red; }
      .cart_content .cart_info .p_number .p_number_right {
        width: 4rem;
        height: 4rem;
        text-align: center; }
        .cart_content .cart_info .p_number .p_number_right img {
          width: 1.8rem;
          height: 1.8rem;
          margin: 0 auto; }
    .cart_content .cart_info .cart_p_num {
      border-bottom: 1px solid #eee; }
      .cart_content .cart_info .cart_p_num p {
        line-height: 2; }
        .cart_content .cart_info .cart_p_num p .price {
          font-size: 2.4rem;
          color: red; }
  .cart_content .cart_list {
    margin-top: 1rem;
    padding: .5rem;
    background: #fff;
    border-radius: .5rem;
    display: flex; }
    .cart_content .cart_list ul {
      width: 100%; }
      .cart_content .cart_list ul .item {
        display: flex;
        width: 100%;
        border-bottom: 1px solid #eee;
        padding: 1rem 0px; }
        .cart_content .cart_list ul .item .left_food {
          flex: 1;
          display: flex; }
          .cart_content .cart_list ul .item .left_food img {
            width: 4rem;
            height: 4rem;
            border-radius: 10%;
            margin-right: .8rem; }
          .cart_content .cart_list ul .item .left_food .food_info {
            flex: 1; }
        .cart_content .cart_list ul .item .right_cart {
          width: 10rem; }
  .cart_content .hot_food {
    background: #fff;
    margin-top: 1rem;
    border-radius: .5rem;
    margin-bottom: 4rem; }
    .cart_content .hot_food h3 {
      font-size: 1.4rem;
      padding: .5rem 0px; }
    .cart_content .hot_food .item_list_outer {
      width: 100%;
      overflow-x: auto; }
      .cart_content .hot_food .item_list_outer .item_list {
        padding: 0px .5rem;
        width: 50rem; }
        .cart_content .hot_food .item_list_outer .item_list li {
          width: 8rem;
          padding: .5rem;
          box-sizing: border-box;
          float: left; }
          .cart_content .hot_food .item_list_outer .item_list li .inner {
            background: #fff;
            width: 100%;
            border-radius: .5rem;
            overflow: hidden; }
            .cart_content .hot_food .item_list_outer .item_list li .inner img {
              width: 100%; }

/*购车加减*/
.cart_num {
  width: 10rem;
  display: flex;
  margin-top: .8rem; }
  .cart_num .input_left, .cart_num .input_right {
    flex: 1;
    width: 2.8rem;
    height: 2.8rem;
    line-height: 2.8rem;
    text-align: center;
    color: red;
    border: 1px solid #eee;
    font-size: 2.4rem; }
  .cart_num .input_center {
    flex: 1; }
    .cart_num .input_center input {
      width: 2rem;
      text-align: center;
      width: 100%;
      height: 2.8rem;
      border: none;
      border-top: 1px solid #eee;
      border-bottom: 1px solid #eee;
      float: left; }

/*购车空*/
.cart_empty {
  text-align: center;
  line-height: 3; }
  .cart_empty h3 {
    font-size: 1.8rem; }

    #cc {
    text-align: center;
    width: 100%;
    height: 2.8rem;
    border: none;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    float: left;
    line-height: 2.8rem;
    }
</style>



