<template>
	<!-- 搜你喜欢 -->
	<div id="search">
		<!-- 头部开始 -->
		<header>
			<div class="header_input">
				<!-- 绑定search并在data定义值为空 -->
				<input type="text" placeholder="请输入菜名" v-model="search" @keyup="seachall()">
			</div>
		</header>
		<!-- 头部结束 -->

		<!-- 主体开始 -->
		<div id="content">
			<ul class="item_list clearfix">
				<!-- 渲染数据 -->
				<li v-for="item in list">
						<img :src="url+'/'+item.caidan_img" alt="">
						<p><b>{{item.caidan_name}}</b></p>
						<p>¥{{item.caidan_jiag}}</p>
						<p>已售：{{item.caidan_flas}}份</p>
				</li>
			</ul>
		</div>
		<!-- 主体结束 -->

		<!-- 底部按钮开始 -->
		<div id="footer">
			
		</div>
		<!-- 底部按钮结束 -->
	</div>
</template>

<script>
	import temles from "@/imgag/temles.js"
	export default{
		data(){
			return{
				search:'',
				url:'http://localhost:8080/web/',
				list:[]
			}
		},
		methods:{
			// getData(){
			// 	//使用get方式获取数据库数据
			// 	this.$http.get('http://localhost:8080/web/caidan.php').then((response)=>{
			// 		console.log(response);
			// 		this.list=response.body;
			// 	},(err)=>{
			// 		console.log(err);
			// 	})
			// },
			//查看菜类详细信息
			seachall(){
				if(this.search!=""){
					var str={
						search:this.search
						}
						this.$http.post('http://localhost:8080/web/search.php',str,{emulateJSON:true}).then((response)=>{
							this.list=response.body;
							console.log(response)
						},(err)=>{
							console.log(err);
						})
					}
				}
		},
		// created(){
		// 	this.getData();
		// },
		computed: {
			//搜索功能函数
			
		}
		}
</script>
<style scoped>
	#search{width: 100%;height: 100%;position: relative;}

	/* 头部开始 */
	#search header{
		position: relative;
		width: 100%;
		height: 80px;
		background: #F7F7F7;
		border-top: 3px solid #ECECEC;
		text-align: center;
    	line-height: 80px;
    	font-weight: bold;
	}
	#search header .header_input{
		position: relative;
		margin: 0 auto;
		top: 50%;
		margin-top: -25px;
		width: 390px;
		height: 50px;
		/* border: 1px solid red; */
	}
	#search header .header_input input{
		position: fixed;
	    top: 0;
	    margin: 0 auto;
	    width: 390px;
	    height: 50px;
	    z-index: 999;
	    left: 50%;
	    margin-left: -195px;
		border-radius: 20px;
		border: 0;
	}
	/* 头部结束 */


	/* 主体开始 */
	#search #content{
		position: relative;
		margin: 0 auto;
		margin-top: 10px;
		width: 95%;
		height: 100%;
		/* border: 1px solid red; */
	}
	#search #content ul{display: block;}
	#search #content ul li{
		width: 100%;
		border-bottom: 1px solid #ffd400;
		display: inherit;
		margin-bottom: 15px;
		float: left;
		/* flex-direction: row; */
	}
	#search #content ul li img{
		display: block;
		float: left;
		padding-bottom: 10px;
	}
	#search #content ul li p{
		line-height: 25px;
		margin-left: 10px;
		display: block;
		padding-left: 120px;
	}
	/* 主体结束 */

	/* 底部按钮开始 */
	#search #footer{
		position: fixed;
		bottom: 0;
		width: 95%;
		height: 75px;
	}
	#search #footer ul{display: block;}
	#search #footer ul li a{display: block;}
	#search #footer ul li:nth-child(1){
		position: absolute;
		left: 0;
		top: 0;
		width: 75px;
		height: 75px;
		border-radius: 50%;
		background: #313131;
	}
	#search #footer ul li:nth-child(2){
		position: absolute;
		left: 50%;
		margin-left: -32.5px;
		width: 75px;
		height: 75px;
		border-radius: 50%;
		background: #313131;
	}
	#search #footer ul li:nth-child(3){
		position: absolute;
		right: 0;
		/* top: 0; */
		width: 75px;
		height: 75px;
		border-radius: 50%;
		background: #F73239;
	}

	#search #footer ul li a .footerfixend_img{
		width: 35px;
	    height: 35px;
	    position: relative;
	    margin: 0 auto;
	    
	}
	#search #footer ul li a .footerfixend_img img{
		width: 100%;height: 100%;display: block;margin-top: 10px;
	}
	#search #footer ul li a .footerfixend_bnt{
		display: block;
		text-align: center;
		margin: 0 auto;
		font-size: 1rem;
		color: white;
		text-decoration: none;
	}
	/* 底部按钮结束 */
</style>




