<template>
  <div id="app">
    <div v-for="item in caidan">
    <div class="gouwu">
      <div class="fh">
        <router-link :to="{path: '/dianchan'}"><p>返回</p></router-link>
      </div>
      <div class="top">
          <img :src="url+item.caidan_img" alt="flowers"><br>
          <h2>{{item.caidan_name}}</h2>
          <p>{{item.caidan_jiag}}/份</p>
      </div>
    </div> 
    <div class="gouwu">
      <h4>商品详情</h4>
      <div class="top">        
            <img :src="url+item.caidan_img" alt="flowers"><br>
            <h3>{{item.caidan_js}}</h3>
            <h3>{{item.caidan_js}}</h3>
      </div>
    </div> 
  </div>
    <footer class="pfooter">
      <p>数量：</p>
        <div id="aa" style="float:left;" @click="a">-</div>
        <span id="cc" style="float:left;">{{msg.num}}</span>
        <div id="bb"style="float:left;" @click="b">+</div>
      <button class="addcart" @click="addcart()">加入购物车</button>			
    </footer>
  </div>
</template>

<script>
  import Storage from "@/imgag/storage.js"
//  import temles from "@/imgag/temles.js"
export default {
  name: 'app',
  data () {
    return {
      caidan:[],
      url:'http://localhost:8080/web/',
      // menuname1:this.$route.params.menuname1,
      // menuname2:this.$route.params.menuname2,
      topone:"../static/img/product.jpg",
      msg: {
                    num: 1,
                    flag: true
                }
    }
  },
  components:{

  },
  methods:{
    gd(){
			this.$http.get('http://localhost:8080/web/pcontent.php',{params:{
					id:this.$route.params.id}},{emulateJSON:true}).then((response)=>{
          console.log(response);
          this.caidan=response.body;
				},(err)=>{
					console.log(err);
				})
		},

    a () {
      if(this.msg.num>=1){
        this.msg.num = this.msg.num - 1;
      }else{
        alert('你玩你MB呢。');
      }
    },

    b () {
      this.msg.num = this.msg.num + 1;
    },
    addcart(){
      var zhuo_id=Storage.get('zhuo_id');
      var diancai_id=Storage.get('diancai_id');
      var str={
        zhuo_id:zhuo_id,
        diancai_id:diancai_id,
        caidan_name:this.caidan[0].caidan_name,
        caidan_jiag:this.caidan[0].caidan_jiag,
        caidan_img:this.caidan[0].caidan_img,
        num:this.msg.num
      }
      this.$http.post('http://localhost:8080/web/addorder.php',str,{emulateJSON:true}).then((success)=>{
          console.log(success); 
         // this.$router.push({name:'cart'});
        },(err)=>{

        })
    }
  },
  created() {
     this.gd();
    }
}
</script>

<style scoped>
    .gouwu {
      margin-bottom: .6rem;
      background: #FFF;
      width: 100%;
    }

    .gouwu>.top img {
      width: 97%;
    }

    .gouwu>.top h2 {
      padding: .2rem .5rem;
      font-size: 1.1rem;
      color: #555;
    }

    .gouwu>.top p {
      font-size: .6rem;
      padding: .2rem .5rem;
      color: red;
    }

    .gouwu>.top h3 {
      margin: 9% 0 0 3%;
      color: #555;
      font-size: .6rem;
    }

    .gouwu h4 {
      color: #555;
      padding: .5rem;
      font-size: 1.2rem;
    }

    .pfooter {
      position: fixed;
      bottom: 0px;
      height: 4.4rem;
      line-height: 4.4rem;
      background: #fff;
      left: 0px;
      width: 100%;
      border-top: 1px solid #eee;
    }

    .pfooter .addcart {
      float: right;
      background: red;
      color: #fff;
      height: 3rem;
      border: none;
      padding: 0 .5rem;
      border-radius: .5rem;
      margin-top: .8rem;
      margin-right: .5rem;
    }

    .pfooter p {
      float: left;
      font-size: 1rem;
      padding: 0rem .5rem;
      color: #555;
    }

    .pfooter>#aa {
      width: 2rem;
      height: 2rem;
      line-height: 1.5rem;
      text-align: center;
      color: red;
      border: 1px solid #eee;
      font-size: 2.4rem;
      margin-top: 1rem;
    }

    .pfooter>#bb {
      width: 2rem;
      height: 2rem;
      line-height: 1.5rem;
      text-align: center;
      color: red;
      border: 1px solid #eee;
      font-size: 2.4rem;
      margin-top: 1rem;
    }

    .pfooter>#cc {  
      margin-top: 1rem;
      text-align: center;
      width: 2rem;
      height: 2rem;
      border-top: 1px solid #eee;
      border-bottom: 1px solid #eee;
      line-height: 2;
    }

    .fh {
      text-align: center;
      position: fixed;
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
      background: #555;
    }

    .fh p {
      line-height: 3rem;
      font-size: .8rem;
      color: #fff;
    }

    .top li {
      list-style: none;
    }
</style>



