<template>
  <div id="app">
    <header>
        <img :src="headerimg" alt="">
        <p>{{pst}}</p>
    </header>
    <content>
        <P>{{conp}}</P>
        <ul>
          <li v-for = "item in lists"  @click="btn_this(item)" :class="(item==num)?'cilcka':''">{{item}}</li>
        </ul>
    </content>
    <footer>
      <p @click='diancan()'>{{fop}}</p>
    </footer>
  </div>
</template>

<script>
  import Storage from "@/imgag/storage.js"
export default {
  name: 'app',
  data () {
    return {
      num:0,
      headerimg:"../static/img/canju.png",
      pst:"用餐人数",
      conp:"请选择正确的用餐人数 ，小二马上给你送餐具",
      lists:['1人', '2人', '3人', '4人', '5人', '6人', '7人', '8人', '9人', '10人', '11人', '12人'],
      fop:"开始点餐"
    }
  },
  components:{

  },
  methods:{
      btn_this(item){
        this.num=item;
      },
      diancan(){
        this.$http.get('http://localhost:8080/web/start.php',
        {params:{zhuo_id:this.$route.params.id,num:this.num}},
        {emulateJSON:true}).then((response)=>{
          console.log(response.body)
          if(response.body.result>=1){
            console.log(response)
            Storage.set('zhuo_id',this.$route.params.id);
            Storage.set('diancai_id',response.body.responce);
            this.$router.push({path:"/dianchan"})
          }else{
            alert(response.body.responce)
          }
        },(err)=>{
          console.log(err)
        }
        )
        // alert(this.num),
        // this.$router.push({name:'dianchan',params:{num:this.num}})
      }
  }
}
</script>

<style scoped>
    * {
      margin: 0px;
      padding: 0px;
    }

    header {
      background: #000;
      width: 32vw;
      height: 10vw;
      margin: 18vw auto 0 auto;
      border-radius: .5rem;
    }

    header img {
      height: 85%;
      margin: 1vw 0vw 0vw 3vw;
      float: left;
    }

    header p {
      font-size: 4.5vw;
      float: left;
      color: #FFF;
      margin: 2.2vw 0vw 0vw 0vw;
    }
    
    content p {
      font-size: 1.5vw;
      color: red;
      text-align: center;
      margin: 1rem 0rem;
    }

    content ul {
      list-style: none;
    }

    content ul li {
      float: left;
      width: 22%;
      height: 10vw;
      line-height: 10vw;
      text-align: center;
      background: #fff;
      border-radius: .5rem;
      border: 1px solid #ccc;
      color: #555;
      font-size: 2vw;
      margin: 5vw 0vw 0vw 2vw;
    }

    footer {
      position: fixed;
      bottom: 5vw;
      left: 50%;
      margin-left: -13vw;
      width: 25vw;
      height: 25vw;
      border-radius: 50%;
      background: red;
      color: #fff;
    }

    footer p {
      
      width: 50%;
      height: 50%;
      margin: 6vw 0vw 0vw 8vw;
    }

    .router {
      color: #fff;
      text-decoration: none;
    }

    .router p {
      font-size: 1rem;
    }

    .cilcka {
      background: red;
    }
</style>



