import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import index from '@/components/index'
import dianchan from '@/components/dianchan'
import gouwuche from '@/components/gouwuche'
import rexiao from '@/components/rexiao'
import shousuo from '@/components/shousuo'
import dianguo from '@/components/dianguo'
import dian from '@/components/dian'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index/:id',
      name: 'index',
      component: index
    },
    {
      path: '/dianchan',
      name: 'dianchan',
      component: dianchan
    },
    {
      path: '/gouwuche/:id',
      name: 'gouwuche',
      component: gouwuche
    },
    {
      path: '/rexiao',
      name: 'rexiao',
      component: rexiao
    },
    {
      path: '/shousuo',
      name: 'shousuo',
      component: shousuo
    },
    {
      path: '/dianguo',
      name: 'dianguo',
      component: dianguo
    },
    {
      path: '/dian',
      name: 'dian',
      component: dian
    }
  ]
})



